*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #282c34;
}
.bmi{
  width: 1000px;
  height: 500px;
  padding: 20px;
  background-color: white;
  border-radius: 8px;
  display: flex;
}
.photo {
  width: 600px;
  background-image: url("assests/image_bmi.avif");
  background-repeat: no-repeat;
  background-position: center;
}
.bmiCalculator{
  width: 500px;
  margin-left: 50px;
}
label{
  margin-bottom: 20px;
  display: block;
}
input{
  margin-bottom: 20px;
  width: 100%;
  padding: 10px;
  border-radius: 4px;
  border: 1px solid gray;
}

.submit{
  background-color: rgb(3, 145, 3);
  border: 1px solid white;
  padding: 15px;
  border-radius: 5px;
  color: white;
  margin-right: 20px;
  cursor: pointer;
}

.clear{
  background-color: rgb(204, 24, 24);
  border: 1px solid white;
  padding: 15px;
  border-radius: 5px;
  color: white;
  cursor: pointer;
}

.para{
  border: 2px solid rgb(145, 142, 142);
  color: black;
  padding: 30px;
  margin-top: 20px;
  width: 100%;
}

h1{
  margin-bottom: 30px;
  text-transform: uppercase;
  font-size: 25px;
  color: rgb(44, 98, 180);
}


.error{
  font-size: 15px;
  margin-bottom: 10px;
  color: red;
}